import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {useHistory} from "react-router-dom"
import {LogOutAction} from "../action/login_action"


export default function Navbar() {
  const history=useHistory()
  const dispatch = useDispatch()
  const Islogin = localStorage.getItem("UserId");
  const [Name, setName] = useState(localStorage.getItem("UserName"));
  const { reduxLogin } = useSelector((state) => state.Login);
  console.log(Islogin);
  console.log(reduxLogin);

  // useEffect(() => {
  //   if(reduxLogin!=[]){
  //     setName(reduxLogin[0].name)
  //   }
  // }, [reduxLogin])
  

  const handleLogout = async()=>{
     dispatch(LogOutAction())
    history.push('/login')
  }

//   useEffect(() => {
//     if (reduxLogin!=[] && reduxLogin!=null) {
//       setName(reduxLogin[0]?.name);
//     }
//   }, [reduxLogin]);

  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-light bg-primary text-dark">
        <div className="container h-25">
          <div className="d-flex flex-column">
            {/* <a className="navbar-brand text-light fs-4" href="#">FlipKart</a> */}
            <Link to="/">
              <img className="img-fluid" src="./image/flipcard.png" alt="img" />
            </Link>
            <p>
              Explore <b className="text-warning">Plus</b>
              <img
                src="./image/plus.png"
                alt=""
                style={{ height: "15px" }}
              />{" "}
            </p>
          </div>

          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="input-group mb-3">
            <input list="brand" className="form-control" />
            <datalist id="brand">
              <option
                className="form-control"
                value="iwatch series 7 smart watch "
              />
              <option className="form-control" value="Macbook pro" />
              <option className="form-control" value="Nike vapour max" />
            </datalist>

            <button
              className="btn btn-light text-primary fw-bold "
              type="button"
              id="button-addon2"
            >
              <i className="bi bi-search"></i>
            </button>
          </div>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              {reduxLogin ? (
                // <h4>
                //     {Name}
                // </h4>
                <li className="nav-item dropdown ms-3 me-3 mb-1 ">
                  <button
                    className=" bg-light nav-link fs-5 fw-bolder ps-4 pe-4 text-primary btn border-0 "
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    {reduxLogin[0].name}
                  </button>
                  <ul
                    className="dropdown-menu"
                    aria-labelledby="navbarDropdown"
                  >
                    <li>
                      <a className="dropdown-item" onClick={handleLogout} >
                        Logout
                      </a>
                    </li>
                    <hr />
                    <li>
                      <a className="dropdown-item" href="#">
                        My profile
                      </a>
                    </li>
                  </ul>
                </li>
              ) : (
                <li className="nav-item dropdown ms-3 me-3 mb-1 ">
                  <button
                    className=" bg-light nav-link fs-5 fw-bolder ps-4 pe-4 text-primary btn border-0 "
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    Login
                  </button>
                  <ul
                    className="dropdown-menu"
                    aria-labelledby="navbarDropdown"
                  >
                    <li className="d-flex">
                      <Link to="/Login" className="dropdown-item">
                        Signup/Login{" "}
                      </Link>
                    </li>
                    <hr />
                    <li>
                      <a className="dropdown-item" href="#">
                        My profile
                      </a>
                    </li>
                    <hr />
                    <li>
                      <a className="dropdown-item" href="#">
                        Orders
                      </a>
                    </li>
                    <hr />
                    <li>
                      <a className="dropdown-item" href="#">
                        Wishlist
                      </a>
                    </li>
                    <li>
                      <hr className="dropdown-divider" />
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        Something else here
                      </a>
                    </li>
                  </ul>
                </li>
              )}

              {/* {Name && (
                <li className="nav-item fs-5 ">
                  <a
                    className="nav-link fs-5 dropdown-toggle text-light"
                    href="#"
                    id="navbarDropdown"
                    role="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <h4>{Name}</h4>
                  </a>
                  <ul
                    className="dropdown-menu"
                    aria-labelledby="navbarDropdown"
                  >
                    <li>
                      <a className="dropdown-item" href="#">
                        Logout
                      </a>
                    </li>
                  </ul>
                </li>
              )} */}

              <li className="nav-item dropdown">
                <a
                  className="nav-link fs-5 dropdown-toggle text-light"
                  href="#"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  More
                </a>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <a className="dropdown-item" href="#">
                      Sell on flipkart
                    </a>
                  </li>
                  <hr />
                  <li>
                    <a className="dropdown-item" href="#">
                      Notification prefrence
                    </a>
                  </li>
                  <hr />
                  <li>
                    <a className="dropdown-item" href="#">
                      <i className="bi bi-question-square"></i> 24 X 7 customer
                      care
                    </a>
                  </li>
                </ul>
              </li>
              <li className="nav-item fs-5 ">
                <a className="nav-link d-flex text-light">
                  <i className="bi bi-cart4"></i> Cart
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}
