import { combineReducers, applyMiddleware,legacy_createStore as createstore} from 'redux'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import { Login} from './reducer/login_reducer'


const rootreducer = combineReducers({ Login: Login, })

const userInfoLocalStorage= localStorage.getItem('loginUser') ? JSON.parse(localStorage.getItem('loginUser')):undefined;

const inital={
    Login:{
        reduxLogin:userInfoLocalStorage
    }
}

const store = createstore(rootreducer, inital, composeWithDevTools(applyMiddleware(thunk)))

export default store


