
import './App.css'
import Navbar from './components/Navbar';
import Home from './pages/Home';
import { BrowserRouter as Router, Route } from "react-router-dom";
import Product from './components/Product';
import Product_mobile from './components/Product_mobile';
import Single_laptop from './components/Single_laptop';
import Single_Mobile from './components/Single_mobile';
import Login from './pages/Login';
import CreateAccount from './pages/CreateAccount';


function App() {
  return (
    <>
      <Router>
        <Navbar />
        <Route component={Home} path='/' exact />
        <Route component={Product} path='/laptop' />
        <Route component={Product_mobile} path='/mobile' />
        <Route component={Single_laptop} path='/single-laptop/:id' />
        <Route component={Single_Mobile} path='/single-Mobile/:id' />
        <Route component={Login} path='/login' />
        <Route component={CreateAccount} path='/CreateAccount' />


      </Router>


    </>
  );
}

export default App;
