import axios from "axios";

export const LoginAction = (UserId, Password) => async (dispatch,getState) => {
  const { data } = await axios.post("http://localhost:5000/api/v1/Login", {
    UserName: UserId,
    Password: Password,
  });
  console.log(data);
  // if(data.data){
  //     localStorage.setItem("UserId",JSON.stringify(data.data._id))
  //     localStorage.setItem("UserName",JSON.stringify(data.data.name))
  // }
  dispatch({
    type: "LOGIN",
    payLoad: { ...data.allData },
  });
  localStorage.setItem("loginUser",JSON.stringify(getState().Login.reduxLogin))

};
export const LogOutAction = () => async (dispatch) => {
      localStorage.removeItem("loginUser")
  dispatch({
    type: "LOGOUT",
    payLoad: "",
  });
};
