import React, { useState, useEffect } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import { Button, notification } from 'antd';


export default function CreateAccount() {
  const [Name, setName] = useState("");
  const [Email, setEmail] = useState("");
  const [Phone, setPhone] = useState("");
  const [Password, setPassword] = useState("");
  let history = useHistory();



  const HandleSubmit = async(e) => {
    e.preventDefault()
    console.log(Name, Email, Phone, Password);
    const { data } = await axios.post("http://localhost:5000/api/v1/CreateAccount",{ Name: Name, Email: Email, Phone: Phone, Password: Password });
    console.log(data.data);
    if(data){
      // notification.success({
      //   message: 'Account Created successfully',
      //   description:
      //     'Please login',
      // });
      history.push("/login")
    }

  };

  // useEffect(async () => {
  //   if(Name){
  //     const { data } = await axios.post(
  //       "http://localhost:5000/api/v1/CreateAccount",
  //       { Name: Name, Email: Email, Phone: Phone, Password: Password }
  //       );
  //       console.log(data.data);
  //     }
  // }, [HandleSubmit]);
  return (
    <div>
      <div className="container">
        <div className="row">
          <div className="col-md-3"></div>
          <div
            className="col-md-5 mt-2 p-5"
            style={{ backgroundColor: "#0d6efd" }}
          >
            <div className="row" style={{ height: "400px" }}>
              <h2>Create Account here</h2>
              <form onSubmit={HandleSubmit}>
                <div className="form-floating mb-3">
                  <input
                    type="text"
                    className="form-control border-0 border-bottom"
                    id="floatingInput"
                    placeholder="Enter your Name"
                    onChange={(e) => setName(e.target.value)}
                  />
                  <label htmlFor="floatingInput">Name</label>
                </div>
                <div className="form-floating mb-3">
                  <input
                    type="email"
                    className="form-control border-0 border-bottom"
                    id="floatingInput"
                    placeholder="name@example.com"
                    onChange={(e) => setEmail(e.target.value)}
                  />
                  <label htmlFor="floatingInput">Email address</label>
                </div>
                <div className="form-floating mb-3">
                  <input
                    type="number"
                    className="form-control border-0 border-bottom"
                    id="floatingInput"
                    placeholder="97....886"
                    onChange={(e) => setPhone(e.target.value)}
                  />
                  <label htmlFor="floatingInput">Mobile no</label>
                </div>
                <div className="form-floating">
                  <input
                    type="password"
                    className="border-0 border-bottom form-control"
                    id="floatingPassword"
                    placeholder="Password"
                    onChange={(e) => setPassword(e.target.value)}
                  />
                  <label htmlFor="floatingPassword">Password</label>
                </div>
                <br />
                <button
                  style={{ backgroundColor: "#fb641b", color: "white" }}
                  className="btn  w-100"
                >
                  Create Account
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
