import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { LoginAction } from "../action/login_action";

export default function Login() {
  const dispatch = useDispatch();
  const { reduxLogin } = useSelector((state) => state.Login);
  let history = useHistory();
  const [UserId, setUserId] = useState("");
  const [Password, setPassword] = useState("");
  console.log("xxxxx", reduxLogin);

  const handleSubmit = async (e) => {
    e.preventDefault();
    await dispatch(LoginAction(UserId, Password));

    if(!localStorage.getItem("loginUser")){
      alert("Email Or Password is incorrect")
    }

    // const {data} = await axios.post("http://localhost:5000/api/v1/Login",{ UserName: UserId, Password: Password });
    // console.log(data);

    // if(data.success){
    //   localStorage.setItem("username",JSON.stringify(data.data.name))
    //   localStorage.setItem("UserId",JSON.stringify(data.data._id))
    // }
  };

  useEffect(() => {
    console.log("xxxxx", reduxLogin);
    if (localStorage.getItem("UserName") && localStorage.getItem("UserId")) {
      history.push("/")
    }
  }, [reduxLogin,localStorage]);

  return (
    <div>
      <div className="container">
        <div className="row">
          <div className="col-md-6 offset-3 mt-2">
            <div className="row" style={{ height: "450px" }}>
              <div
                className="col-md-5 bg-primary text-light"
                style={{
                  borderTopRightRadius: "30px",
                  borderBottomLeftRadius: "30px",
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "space-around",
                }}
              >
                <div>
                  <h4>Login</h4>
                  <p>Get access to your Orders, Wishlist and Recommendations</p>
                </div>

                <img src="./Image/login.png" className="img-fluid " alt="" />
              </div>
              <div className="col-md-7 pt-3">
                <form onSubmit={handleSubmit}>
                  <div className="form-floating mb-3">
                    <input
                      type="email"
                      className="form-control border-0 border-bottom"
                      id="floatingInput"
                      placeholder="name@example.com"
                      onChange={(e) => setUserId(e.target.value)}
                    />
                    <label htmlFor="floatingInput">
                      Email address/Mobile no
                    </label>
                  </div>
                  <div className="form-floating">
                    <input
                      type="password"
                      className="border-0 border-bottom form-control"
                      id="floatingPassword"
                      placeholder="Password"
                      onChange={(e) => setPassword(e.target.value)}
                    />
                    <label htmlFor="floatingPassword">Password</label>
                    <span style={{ fontSize: "12px" }}>
                      {" "}
                      By continuing, you agree to Flipkart's Terms of Use and
                      Privacy Policy. <a href="#">Forgot password?</a>
                    </span>
                  </div>
                  <br />
                  <button
                    style={{ backgroundColor: "#fb641b", color: "white" }}
                    className="btn  w-100"
                    type="submit"
                  >
                    Login
                  </button>
                  <p className="text-center">OR</p>
                </form>
                <button
                  style={{ color: "blue", boxShadow: "5px 5px 5px black" }}
                  className="btn btn-light w-100"
                  onClick={() => {
                    history.push("/CreateAccount");
                  }}
                >
                  New to Flipkart? Create an account
                </button>
              </div>
            </div>
            <div className="border border-1"></div>
          </div>
        </div>
      </div>
    </div>
  );
}
