import React from 'react'
import { Link } from 'react-router-dom'

export default function Laptop({ item }) {
    return (

        <div className="row mt-1">
            <div className="border-bottom" >
                <div className="row g-0">
                    <Link to={"/single-laptop/" + item._id} className="col-md-4">
                        <img src={item.image} className="img-fluid rounded-start" alt="..." />
                    </Link>
                    <div className="col-md-6">
                        <div className="card-body">
                            <h5 className="card-title">{item.name}</h5>
                            <ul>
                                <li>Brand:{item.brand}</li>
                                <li>Category:{item.category}</li>
                            </ul>
                            <p className="card-text">{item.description}</p>
                            <p className="card-text"><small className="text-muted">Stock:{item.stock}</small></p>
                        </div>
                    </div>
                    <div className="col-md-2">
                        <div className="card-body fs-3">
                            <i className="bi bi-currency-dollar"></i>:{item.price}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
