export const Login = (state = { reduxLogin: [] }, action) => {
  const item = action.payLoad;
  switch (action.type) {
    case "LOGIN":
      return {
        reduxLogin: [action.payLoad],
      };
    case "LOGOUT":
      return {
        reduxLogin: undefined,
      };
    default:
      return state;
  }
};
